@import "../../App.scss";

.btn {
    padding: 10px 20px;
    font-size: 14px;
    letter-spacing: 0.5px;
    transition: all 0.5s;
    font-weight: 400;
    line-height: 1.5;
    border-radius: 0.25rem;
    border: 1px solid transparent;
    background-color: $primary-color;

    .feature-list{
        .feature-item{
            color: blue;

            &::marker {
                color: blue;
                font-size: 1.2em;
              }
        }
    }
}
